import React , {useState} from 'react';
import { db } from '../firebase'
import { id32 } from '@jsweb/randkey'

import '../CSS/AddButton.css'

const AddButton = ({uid}) => {
	const [title , setTitle] = useState("Title")
	const [desc , setDesc] = useState("Description")

	const onClick = () => {
		const key = id32();
		db.collection("userTodos").doc(uid).collection("todos").doc(key).set({
			title: title,
			desc: desc,
			done: false,
			id: key
		})
		.then( docRef => {
			setTitle("")
			setDesc("")
		})
	}

  return (
  	<div className="addBtn-main">
  		<div className="add-btn-inp-main">
  			<input onChange={(e) => setTitle(e.target.value)} value={title} className="add-btn-inp"/>
  			<input onChange={(e) => setDesc(e.target.value)} value={desc} className="add-btn-inp addBtn-desc"/>
  		</div>
	    <div onClick={onClick} className="addBtn-main-btn">
	    	<h1 className="addBtn-m-text">+</h1>
	    </div>
	  </div>
  )
}

export default AddButton;