import React , { useState } from 'react';
import Login from './Components/Login'
import Main from './Containers/Main'
import Header from './Components/Header'
import './CSS/App.css'

const App = () => {
  const [logged , setLogged] = useState(false)
  const [user , setUser] = useState()

  return (
    <div>
      {!logged ? (
        <Login setUser={setUser} logged={logged} setLogged={setLogged}/>
      ) : (
        <div className="main">
          <Header />
          <Main user={user}/>
        </div>
      )}
    </div>

//   <div>
//     <Header />
//     <Main uid={"B76LHAEXbSWDV54qI7oIWl5Ali72"} />
//   </div>
  );
}

export default App;