import React , {useState} from 'react';
import Tick from '../images/tick.png'
import X from '../images/x.png'
import {db} from '../firebase'

import '../CSS/ToDo.css'

const ToDo = ( {done , uid , title , desc , id} ) => {

  const onDelete = () => {
    db.collection("userTodos").doc(uid).collection("todos").doc(id).delete()
  }
  
  const onMarkDone = () => {
    db.collection("userTodos").doc(uid).collection("todos").doc(id).set({
      done: !done
    }, {merge: true})
  }

  const [open , setOpen] = useState(false);
  const onClick = () => setOpen(!open)

  return (
    <div className="toDo-big">
      <div onClick={onClick} className={`toDo-main ${ open === true ? 'open' : 'closed' }`}>
        <h3 className={`toDo-text ${ open === true ? 'toDo-t-o' : 'toDo-t-c' }`}>
        <div >{title}</div>
        {open === true ? (
          <div className="toDo-popUp">
            {desc}
            <div className="toDo-p-btn-main">
              <button onClick={onMarkDone} className="toDo-p-btn">Toggle done</button>
              <button onClick={onDelete} className="toDo-p-btn">Delete</button>
            </div>
          </div>
        ) : ''}
        </h3>

        <div className="toDo-image">
          {done ? (
            <img alt="done" className="google-icon" src={Tick}/>
          )
          : 
          (
            <img alt="not-done" className="google-icon" src={X}/>
          )}
        </div>
      </div>
   </div>
  )
}

export default ToDo;